import { ensureElement } from "../../utils/utils";
import { View } from "../base/View";
export class SuccessView extends View {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.events = events;
        this._totalPriceDescription = ensureElement('.order-success__description', this.container);
        const buttonClose = ensureElement('.order-success__close', this.container);
        buttonClose.addEventListener('click', () => {
            events.emit('order:success');
        });
    }
    set description(value) {
        this._totalPriceDescription.textContent = `Списано ${value} синапсов`;
    }
}
