export class LarekApi {
    constructor(baseApi) {
        this._baseApi = baseApi;
    }
    getProductList() {
        return this._baseApi.get('/product/')
            .then(data => (Object.assign(Object.assign({}, data), { items: data.items.map(item => (Object.assign(Object.assign({}, item), { image: this._baseApi.cdn + item.image.replace('.svg', '.png') }))) })));
    }
    postOrder(orderData) {
        return this._baseApi.post('/order', orderData, 'POST');
    }
}
