import { View } from "../base/View";
import { ensureElement } from "../../utils/utils";
export class PageView extends View {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.events = events;
        this.gallery = ensureElement('.gallery', this.container);
        this._counter = ensureElement('.header__basket-counter', this.container);
        this._wrapper = ensureElement('.page__wrapper', this.container);
        this._basket = ensureElement('.header__basket', this.container);
        this._basket.addEventListener('click', () => {
            this.events.emit('basket:open');
        });
    }
    set catalog(items) {
        this.gallery.replaceChildren(...items);
    }
    set counter(value) {
        this._counter.textContent = String(value);
    }
    set locked(value) {
        if (value) {
            this._wrapper.classList.add('page__wrapper_locked');
        }
        else {
            this._wrapper.classList.remove('page__wrapper_locked');
        }
    }
}
