import { ensureElement } from '../../utils/utils';
import { CardView } from '../common/CardView';
export class BasketItemView extends CardView {
    constructor(container, events) {
        super(container, events);
        this._itemCounter = ensureElement('.basket__item-index', this.container);
        const button = ensureElement('.basket__item-delete', this.container);
        button.addEventListener('click', () => {
            events.emit('basketItem:delete', { id: this._id });
        });
    }
    set ItemCounter(value) {
        this._itemCounter.textContent = String(value);
    }
}
