import { View } from '../base/View';
import { ensureElement, createElement } from '../../utils/utils';
export class BasketView extends View {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this._total = ensureElement('.basket__price', this.container);
        this._button = ensureElement('.basket__button', this.container);
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('order:open');
            });
        }
        this.items = [];
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
            this._button.disabled = false;
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста'
            }));
            this._button.disabled = true;
        }
    }
    set total(total) {
        if (total === 1)
            this._total.textContent = `${total} синапс`;
        if (total === 2 || total === 3 || total === 4)
            this._total.textContent = `${total} синапса`;
        this._total.textContent = `${total} синапсов`;
    }
}
