import { ensureElement } from "../../utils/utils";
import { FormView } from "../common/FormView";
export class FormOrderView extends FormView {
    constructor(container, events) {
        super(container, events);
        this.cardButton = ensureElement('[name="card"]', this.container);
        this.cashButton = ensureElement('[name="cash"]', this.container);
        this._address = ensureElement('[name="address"]', this.container);
        this.cardButton.addEventListener('click', () => {
            events.emit('payment:changed', { payment: 'card' });
        });
        this.cashButton.addEventListener('click', () => {
            events.emit('payment:changed', { payment: 'cash' });
        });
    }
    set address(text) {
        this._address.value = text;
    }
    set payment(value) {
        this.cardButton.classList.toggle('button_alt-active', value === 'card');
        this.cashButton.classList.toggle('button_alt-active', value === 'cash');
    }
}
