import { Model } from './base/Model';
export class ProductModel extends Model {
    constructor() {
        super(...arguments);
        this._catalog = [];
        this._basket = [];
        this.order = { payment: null, address: '', email: '', phone: '' };
    }
    set catalog(items) {
        this._catalog = items;
        this.events.emit('catalogItems:changed');
    }
    get catalog() {
        return this._catalog;
    }
    getCatalogItem(id) {
        return this._catalog.find(item => item.id === id);
    }
    addItemBasket(id) {
        if (!this._basket.includes(id)) {
            this._basket.push(id);
            this.events.emit('basket:changed');
        }
    }
    removeItemBasket(id) {
        if (this._basket.includes(id)) {
            this._basket = this._basket.filter(item => item !== id);
            this.events.emit('basket:changed');
        }
    }
    isBasketItem(id) {
        return this._basket.some(itemId => itemId === id);
    }
    isCanBuy(id) {
        return !this._basket.some(itemId => itemId === id) && this._catalog.find(item => item.id === id).price !== null;
    }
    resetBasket() {
        this._basket = [];
        this.events.emit('basket:changed');
    }
    get basket() {
        return this._basket;
    }
    get total() {
        const items = this._basket.map(id => {
            return this._catalog.filter(card => {
                return card.id === id;
            });
        });
        const total = items.flat().reduce((acc, item) => acc + item.price, 0);
        return total;
    }
    get address() {
        return this.order.address;
    }
    get email() {
        return this.order.email;
    }
    get phone() {
        return this.order.phone;
    }
    get orderErrors() {
        if (this.formOrderErrors) {
            return Object.values(this.formOrderErrors);
        }
    }
    get contactsErrors() {
        if (this.formContactsErrors) {
            return Object.values(this.formContactsErrors);
        }
    }
    set payment(value) {
        this.order.payment = value;
        this.events.emit('orderInput:changed', this.order);
    }
    get payment() {
        return this.order.payment;
    }
    setOrderField(field, value) {
        this.order[field] = value;
        this.events.emit('orderInput:changed', this.order);
    }
    validateOrder() {
        const errors = {};
        if (!this.order.payment) {
            errors.payment = 'Укажите способ оплаты. ';
        }
        if (!this.order.address) {
            errors.address = 'Укажите адрес. ';
        }
        this.formOrderErrors = errors;
        return Object.keys(errors).length === 0;
    }
    setContactsField(field, value) {
        this.order[field] = value;
        this.events.emit('contactsInput:changed');
    }
    validateContacts() {
        const errors = {};
        if (!this.order.email) {
            errors.email = 'Укажите почту. ';
        }
        if (!this.order.phone) {
            errors.phone = 'Укажите номер телефона. ';
        }
        this.formContactsErrors = errors;
        return Object.keys(errors).length === 0;
    }
    resetForms() {
        this.order.address = '';
        this.order.payment = null;
        this.order.email = '';
        this.order.phone = '';
    }
    reset() {
        this.resetForms();
        this.resetBasket();
    }
}
